body {
  background: #9266ff;
}

.todo-app {
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input[type="text"] {
  background-color: rgb(27,112, 137);
  border: 0;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;
}

#to-do-form input[type="text"]::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

#to-do-form input:focus {
  outline: none;
}

#to-do-form input[type="submit"] {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: none;
  background-color: rgb(202, 156, 24);
  cursor: pointer;
  font-weight: bold;
  color: #fff;
}

